.App {
  background-image: url('background.24fc77fe.png');
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#root {
  height: 100vh;
  width: 100vw;
  text-align: center;
  color: white;
}
#quote-box {
  height: 47vh;
  line-height: 150%;
  width: 45vw;
  padding: 5vh 5vw 5vh 5vw;
  margin: 0;
  background-color: slateblue;
  font-family: 'Montserrat', 'serif';
  border-radius: 10px;
  position: relative;
}
#new-quote {
  background-color: yellow;
  position: absolute;
  bottom: 0;
  right: 22vw;
  height: 70px;
  width: 150px;
  margin: 10vh .1vw 5vh 0;
  font-size: 80%;
  font-weight: bold;
  color: blue;
  border: blue 5px solid;
  font-family: 'Montserrat', 'serif';
  border-radius: 5px;
}